import React from 'react'

class ContactScreen extends React.Component{
    
    constructor() {
        super()
        this.state = {
            'entry.1824434011': "",
            'entry.338736048': "",
            'entry.1798248693': '',
            'entry.554368173': '',
            submited: false
        }
        this.handleChange = this.handleChange.bind(this)
    }
    
    
    handleChange(event){
        const {name, value} = event.target
        this.setState({
            [name]: value
        })
    }
    
    render(){
        return(
            <div className='contactScreen'>
                <div className='contactLower'>
                    <div className='contactForm'>
                    <h3>Napíšte nám</h3>
                    <p>Neváhajte nás kontaktovať cez tento formulár!</p>
                        <form action="https://docs.google.com/forms/d/e/1FAIpQLSdAUJ2QtRi3WR6Y5nHfwRjaFfjd-82g2row_8xf0NS1ltR5sA/formResponse?" target='hidden_iframe' onSubmit={() =>{this.setState({submitted:true})
                        }} encType="text/plain">
                            <input
                                type="text" 
                                value={this.state.name}
                                    id='entry.1824434011'
                                 
                                name="entry.1824434011"
                                placeholder="Meno a priezvisko" 
                                onChange={this.handleChange}
                                ></input>
                           <input
                                type="email" 
                                value={this.state.email} 
                                name="entry.338736048" 
                                    id='entry.338736048'
                                
                                placeholder="Emailová adresa" 
                                onChange={this.handleChange}
                                ></input>
                           <input
                                type="tel" 
                                value={this.state.phone} 
                                name="entry.1798248693" 
                                    id='entry.1798248693'
                                
                                placeholder="Telefónne číslo" 
                                onChange={this.handleChange}
                                ></input>
                            <textarea
                                type="text" 
                                value={this.state.message} 
                                name="entry.554368173" 
                                    id='entry.554368173'
                                
                                placeholder="Správa" 
                                onChange={this.handleChange}
                                ></textarea>
                            <input className='submitButton' type="submit" value="Odoslať"></input>
                        </form>
                        <iframe name="hidden_iframe" id="hidden_iframe" style={{display:'none'}} onLoad={() => {if(this.state.submitted) {alert(1)}}}></iframe>
                    </div>                  
                    <div className='contactUpper'>
                        <h3>Kontaktné údaje</h3>
                        <div><i className="fas fa-map-marker-alt"></i>
                            <div><a href='https://www.google.sk/maps/place/Sl%C3%A1dkovi%C4%8Dova+23,+974+05+Bansk%C3%A1+Bystrica/@48.7135343,19.1304355,17z/data=!3m1!4b1!4m5!3m4!1s0x47153ddbbd27367d:0x5df9d1919dae77ce!8m2!3d48.7135308!4d19.1326242' target='_blank'>Sládkovičova 23 <br></br> 974 01 Banská Bystrica</a></div>
                        </div>
                        <div><i className="fas fa-map-marker-alt"></i>
                            <div><a href='https://www.google.sk/maps/place/MindIT,+s.r.o./@48.1522726,17.1781763,17z/data=!3m1!4b1!4m5!3m4!1s0x476c8f2518a26b35:0xc83037d06cb1dd42!8m2!3d48.152269!4d17.180365' target='_blank'>Strojnícka 34 <br></br> 821 05 Bratislava</a></div>
                        </div>
                        <div><i className="fas fa-envelope"></i>
                            <div><a href='mailto:info@mindit.sk'>info@mindit.sk</a></div>
                        </div>
                        <div><i className="fas fa-envelope"></i>
                            <div><a href='mailto:procurement@mindit.sk'>procurement@mindit.sk</a></div>
                        </div>
                        <div><i className="fas fa-envelope"></i>
                            <div><a href='mailto:invoices@mindit.sk'>invoices@mindit.sk</a></div>
                        </div>
                        <div><i className="fas fa-phone"></i>
                            <div><a href='tel:+421918681056'>+421 918 681 056</a></div>
                        </div>
                        <div className='extraInfo'>
                            <div>IČO: 47367873</div>
                            <div>DIČ: 2023833482</div>
                            <div>IČ DPH: SK2023833482</div>
                        </div>
                    </div>
                </div>
                <div className='contactUpperMobile'>
                        <h3>Kontaktné údaje</h3>
                        <div><i className="fas fa-map-marker-alt"></i>
                            <div>Sládkovičova 23 <br></br> 974 01 Banská Bystrica</div>
                        </div>
                        <div><i className="fas fa-map-marker-alt"></i>
                            <div>Strojnícka 34 <br></br> 821 05 Bratislava</div>
                        </div>
                        <div><i className="fas fa-envelope"></i>
                            <div><a href='mailto:info@mindit.sk'>info@mindit.sk</a></div>
                        </div>
                        <div><i className="fas fa-envelope"></i>
                            <div><a href='mailto:procurement@mindit.sk'>procurement@mindit.sk</a></div>
                        </div>
                        <div><i className="fas fa-envelope"></i>
                            <div><a href='mailto:invoices@mindit.sk'>invoices@mindit.sk</a></div>
                        </div>
                        <div><i className="fas fa-phone"></i>
                            <div><a href='tel:+421918681056'>+421 918 681 056</a></div>
                        </div>
                        <div className='extraInfo'>
                            <div>IČO: 47367873</div>
                            <div>DIČ: 2023833482</div>
                            <div>IČ DPH: SK2023833482</div>
                        </div>
                    </div>
            </div>
        )
    }
}

export default ContactScreen