class ServicesContent extends React.Component{
    
    constructor(){
        super()
    }
        
    
    componentDidUpdate(){
        if ($('.content').length>0 && this.props.swipeState == 0){
            $("#root").stop().animate({
                scrollTop: $('.content').position().top
            }, 800, function() {})
        }
    }
    
    render(){
        const screens = [
             {src:<div></div>},
             {src:<div className='content'>
                 <ServicesBenefits data={benefitsContents[0]}></ServicesBenefits>
             </div>},
             {src:<div className='content'>
                 <ServicesBenefits data={benefitsContents[1]}></ServicesBenefits>
             </div>},
             {src:<div className='content'>
                 <ServicesBenefits data={benefitsContents[2]}></ServicesBenefits>
             </div>},
            {src:<div className='content'>
                 <ServicesBenefits data={benefitsContents[3]}></ServicesBenefits>
             </div>}
        ]
        return(
            <div className='servicesContent'>
                {screens[this.props.currentContent].src}
            </div>
        )
    }
}


    

export default ServicesContent