import React from 'react'
import { Link } from "react-router-dom";

class ReferencesCont extends React.Component{
    
    constructor() {
        super()
    }
    
    render(){
        return(
            <Link to={this.props.data.href} className='referencesCont'>
                <img src={require( "" + this.props.data.src )}></img>
                <p>{this.props.data.text}</p>
                <div>{this.props.data.type}</div>
            </Link>
        )
    }
}

export default ReferencesCont