import React from 'react'

class Research2 extends React.Component{
    constructor(){
        super()
    }
    render(){
        return(
        <div className='references-cont content'>
        <img className='back-svg' src={require('../references/img/back2.svg')}></img>
        <h1>Automatizované spracovanie neštrukturovaného obsahu</h1>
          <div class="references-content">
           <div class="bonus-info">
               
               <div class="info-cont res">
                   <h3>Momentálny stav</h3>
                        <p>Projekt ukončený</p>
                        <h3>Obdobie realizácie projektu</h3>
                        <p>07/2017 – 06/2021</p>
                        <h3>Projekt mimoriadne ukončený ku dňu</h3>
                        <p>9.5.2018</p>
                        <h3>Partneri</h3>
                        <p>Centrum experimentálnej medicíny Slovenskej akadémie vied <br></br> Chemický ústav Slovenskej akadémie vied <br></br> MindIT, s.r.o. <br></br> Univerzita Komenského v Bratislave</p>
                        <h3>Miesta realizácie</h3>
                        <p>Banská Bystrica – Majerská cesta 36<br></br>Žilina<br></br>Bratislava – mestská časť Karlova Ves</p>
               </div>
           </div>
            <div class="main-info">
                <h3>Výskum</h3>
                <p>Spoločnosť MindIT, s.r.o. je iniciátorom projektu pre spracovanie neštruktúrovaných digitálnych údajov vo forme audio, video súborov a textových dokumentov s využitím hlbokého strojového učenia. Na výskumnom projekte spolupracuje s vedeckými kapacitami zo Slovenskej technickej univerzity v Bratislave, Univerzity Komenského v Bratislave a Centrom vedecko-technických-informácií SR.</p>
                <p>Cieľom výskumného projektu je nadobudnúť takú úroveň poznatkov a postupov v oblasti spracovania jazyka, aby bolo možné následne tieto nadobudnuté vedomosti a postupy využiť pri rozvoji produktov a služieb s vyššou pridanou hodnotou spracovania obsahu v praxi, tzn. na posilnenie hospodárskeho postavenia spoločnosti.</p>
                                <h3>Cieľ projektu</h3>
                                 <p>Hlavným cieľom projektu je aplikovať výsledky výskumu v oblasti hlbokého strojového učenia a neurónových sietí do praxe výberom postupov, ktoré majú najvyššiu mieru presnosti v oblasti spracovanie prirodzeného jazyka a obrazu a súčasne overiť hranice tejto technológie. Splnenie tohto cieľa umožní aplikovanie výsledkov do softvérových produktov a služieb v oblasti archívov, CMS, CRM čím sa podstatne zvýši pridaná hodnota a konkurencie schopnosť firemných produktov a služieb.</p><p>

MindIT, s.r.o. má záujem s hore uvedenými technológiami preniknúť aj na medzinárodné trhy, pričom automatizované spracovanie neštruktúrovaného obsahu s overenou mierou úspešnosti spracovania bude hlavným rozdielovým faktorom.</p>
                <div className='researchGarants'>
                    <img className='research refImg opvai' src={require('./img/opvai_logo.png')}></img>
                    <img className='research refImg' src={require('./img/msvvs_logo.png')}></img>
                    <img className='research refImg' src={require('./img/fond_logo.jpg')}></img>
                    <img className='research refImg' src={require('./img/vyskumna_agentura_logo.png')}></img>
            </div>
            </div>
        </div>
        </div>
        )
    }
}

export default Research2