import React from 'react'
import { Link } from "react-router-dom";
import DelayLink from './../DelayLink.jsx'


class MenuClick extends React.Component{
    constructor(){
        super()
        
    }
    
    render(){
        return(
        <DelayLink to={this.props.href} delay={0} className="menuclick" hideMenu={this.props.hideMenu}>
            <li className="menuitem " style={{color: this.props.active ? '#3487DD' : 'black', pointerEvents: this.props.active ? 'none' : 'auto'}} >{this.props.name}</li>
        </DelayLink>
    )
    }
}

export default MenuClick