import React from 'react'
import ServicesBox from './ServicesBox.jsx'
import ServicesContent from './ServicesContent.jsx'

//child of Slider

class ServiceScreen extends React.Component{
    constructor(){
        super()
        this.state = {currentContent:0}
        this.setContent = this.setContent.bind(this)
    }
    
    
    checkForContentChange(){
         if(this.props.section){
            const sections = ['aihub','data-a-procesy','vyvoj-na-mieru']
            for (const [index, dest] of sections.entries()) {
                if (dest==this.props.section && (index+1)!= this.state.currentContent){
                    this.setContent(index+1)
                    break
                }
            }
        }
        else if(this.state.currentContent!=0){
            this.setContent(0)
        }
    }
    
    setContent(id){
        this.setState({currentContent:id})
    }
    
    render(){
        this.checkForContentChange()
        return(
            <div className='servicesScreen' >
                <div className='servicesCont'>
                    <ServicesBox setContent={this.setContent} href='/sluzby/aihub' id='1' src='./img/iconAI.png' title='AI Hub' />
                    <ServicesBox setContent={this.setContent} href='/sluzby/data-a-procesy' id='3' src='./img/iconAnalysis.png' title='Dáta a procesy' />
                    <ServicesBox setContent={this.setContent} href='/sluzby/vyvoj-na-mieru' id='4' src='./img/iconSolutions.png' title='Vývoj na mieru' />*
                </div>
                {!this.props.swipeState &&
                <ServicesContent swipeState={this.props.swipeState} currentContent = {this.state.currentContent} />
                    }
            </div>
        )
    }
}


export default ServiceScreen