import React from 'react'
import BenefitsBox from './BenefitsBox.jsx'



function ServicesBenefits (props){
    
    return (
        <div className= {`benefitsCont ${props.data.main.bgclass}`}>
            <div>
                <div className='benefitsHeading'>
                   <div className='headings-cont'>
                    {props.data.main.subh ? <h2> {props.data.main.heading} </h2> : <h2 className='solo'> {props.data.main.heading} </h2>}
                    {props.data.main.subh && <h3> {props.data.main.subh} </h3>}
                    </div>
                    <img src={require('./img/heading.svg')}></img>
                </div>
            </div>
            <div className='benefitsBoxCont'>
                <BenefitsBox data={props.data.box1}></BenefitsBox>
                <BenefitsBox data={props.data.box2}></BenefitsBox>
                <BenefitsBox data={props.data.box3}></BenefitsBox>
                <BenefitsBox data={props.data.box4}></BenefitsBox>
            </div>
        </div>
    )
}


export default ServicesBenefits