import React from 'react'
import { Link } from "react-router-dom";

class ServiceBox extends React.Component{
    constructor(){
        super()
        
    }
    render(){
        return(
            <Link to={this.props.href} className='servicesBox' >
                <img src={require( "" + this.props.src )} alt='imagealt'></img>
                <h2>{this.props.title}</h2>
                {this.props.stage && <h3>{this.props.stage}</h3>}
                <div>viac informácií</div>
            </Link>
        )
    }
}


export default ServiceBox