import React from 'react'

function BenefitsBox(props){
    return(
        <div className='benefitsBox '>
            <ion-icon name={props.data.src}></ion-icon>
            <div className='content'>    
                <h2>{props.data.title}</h2>
                <p>{props.data.text}</p>
            </div>
        </div>
    )
}

export default BenefitsBox