import React from 'react'
import ServicesBox from '../services/ServicesBox.jsx'
import ResearchContent from './ResearchContent.jsx'

class ResearchScreen extends React.Component{
      
    constructor(){
        super()
        this.state = {currentContent:0}
        this.setContent = this.setContent.bind(this)
    }
    
    setContent(id){
        this.setState({currentContent:id})
    }
    
    checkForContentChange(){
         if(this.props.section){
            const sections = ['spracovanie-nestrukturovaneho-obsahu','biomedires']
            for (const [index, dest] of sections.entries()) {
                if (dest==this.props.section && (index+1)!= this.state.currentContent){
                    this.setContent(index+1)
                    break
                }
            }
        }
        else if(this.state.currentContent!=0){
            this.setContent(0)
        }
    }
    
    render(){
        this.checkForContentChange()
        return(
            <div className='researchScreen'>
                <div className='researchCont'>
                    <ServicesBox setContent={this.setContent} id='1' src='./img/iconRes.png' title='Automatizované spracovanie neštrukturovaného obsahu' href='/vyskum/spracovanie-nestrukturovaneho-obsahu' />
                    <ServicesBox setContent={this.setContent} id='2' src='./img/iconBio.png' title='Centrum pre biomedicínsky výskum – BIOMEDIRES' href='/vyskum/biomedires'/>
                </div>
                {!this.props.swipeState &&
                <ResearchContent swipeState={this.props.swipeState} currentContent = {this.state.currentContent} />
                    }
            </div>
        )
    }
}

export default ResearchScreen