import React from 'react'
import $ from 'jquery'
import ReferenceTemplate2 from './ReferenceTemplate2.jsx'

import ReferenceTemplate from './ReferenceTemplate.jsx'

import {data} from './referencesData.jsx'
class ReferencesContent extends React.Component{
    
    constructor(){
        super()
    }
        
    
    componentDidUpdate(){
        if ($('.content').length>0 && this.props.swipeState == 0){
            $("#root").stop().animate({
                scrollTop: $('.content').position().top
            }, 800, function() {})
        }
    }
    
    render(){
        const screen = <ReferenceTemplate data={data[this.props.currentContent-1]} />
        return(
            <div className='servicesContent'>
                {this.props.currentContent!=0 && screen}
            </div>
        )
    }
}


    

export default ReferencesContent