class ReferencesScreen extends React.Component{
    
    constructor() {
        super()
        this.state = {
            currentScreen : 0,
            currentContent : 0
        }
        this.preventScroll = false
        this.setContent = this.setContent.bind(this)
        this.setReferences = this.setReferences.bind(this)
    }
    
    setReferences(id){
        if (this.state.currentScreen != id){
        this.preventScroll = true
        let setstate = ()=>{
            return Promise.resolve(this.setState({currentScreen : id, currentContent : 0}
            ))
        }
        setstate().then(() =>{
            this.preventScroll = false
            }
        )
            
        }
    }
    
    checkForContentChange(){
        if(this.props.section){
            const sections = ['slovenska-sporitelna','veolia','itb','erste-group','cestovny-ruch','financna-sprava','ministerstvo-vnutra','ministerstvo-spravodlivosti','ministerstvo-kultury','roche']
            for (const [index, dest] of sections.entries()) {
                if (dest==this.props.section && (index+1)!= this.state.currentContent){
                    this.setContent(index+1)
                    break
                }
            }
        }
        else if(this.state.currentContent!=0){
            this.setContent(0)
        }
    }
    
    setContent(id){
        this.setState({currentContent:id})
    }
    
    
    render(){
        if (!this.preventScroll){
            this.checkForContentChange()
        }
        var thisref = this
        const mq = window.matchMedia('(min-width: 900px)')
        const referencesToRender = referenceSets[this.state.currentScreen].map((item) => {
            return (
            <ReferencesCont setContent={thisref.setContent} key={item.id} data={item} />
            )
        })
        
        const swiperSlides = []
        for (let i=0;i<Math.ceil(referencesToRender.length/6);i++){
            
            swiperSlides.push(<SwiperSlide><div className='swiperCont'>{referencesToRender.slice(i*6,((i+1)*6))}</div></SwiperSlide>)
        }
        
        
        
        const buttonsToRender = buttonsSet.map((item) => {
            return (
            <ChooserButton 
                key={item.id} 
                data={item} 
                onclick={this.setReferences} 
                active={this.state.currentScreen == item.id ? true : false}
            ></ChooserButton>
            )
        })
        
        return(
                <div className='referencesScreen'>

                    <div className='referencesMainCont'>
                    <div style={{display:'flex',justifyContent:'center',width:'100%'}}>
                        {mq.matches ? <div className='referencesChooser'>
                            {buttonsToRender}
                        </div> :
                        <div style={{width:'80%',marginTop:'110px',marginLeft:'auto',marginRight:'auto',maxWidth:'350px'}}>
                            <SliderChooser data={{onSwipe:this.setReferences,startId:0,chooserItems:chooserItems}}></SliderChooser>
                        </div>}
                    </div>
                    <div style={{display:'flex',justifyContent:'center',width:'100%',marginTop:'10px'}}>
                        <div className='referencesWrapper'>
                               {(mq.matches && referencesToRender.length>6) ? <Swiper
      spaceBetween={0}
      slidesPerView={1}
      navigation
      pagination={{ clickable: true }}
      loop={true}
      onSlideChange={() => {}}
    >
      {swiperSlides}
    </Swiper> : referencesToRender}
                                
                        </div>
                    </div>
                </div>
                {!this.props.swipeState &&
                <ReferencesContent swipeState={this.props.swipeState} currentContent = {this.state.currentContent} />
                    }
                </div>
        )
    }
}

export default ReferencesScreen