import React from 'react'
import ServicesBox from '../services/ServicesBox.jsx'
import ProductsContent from './ProductsContent.jsx'


//child of Slider

class ProductsScreen extends React.Component{
    constructor(){
        super()
        this.state = {currentContent:0}
        this.setContent = this.setContent.bind(this)
    }
    
    
    checkForContentChange(){
         if(this.props.section){
            const sections = ['mindrec','archivex','docminer','mindfind']
            for (const [index, dest] of sections.entries()) {
                if (dest==this.props.section && (index+1)!= this.state.currentContent){
                    this.setContent(index+1)
                    break
                }
            }
        }
        else if(this.state.currentContent!=0){
            this.setContent(0)
        }
    }
    
    setContent(id){
        this.setState({currentContent:id})
    }
    
    
    render(){
        this.checkForContentChange()
        return(
            <div className='productsScreen' >
                <div className='productsCont'>
                    <ServicesBox setContent={this.setContent} href='/produkty/mindrec' id='1' src='./img/iconMindRec.png' title='MindRec' stage='Riešenie pre správu registratúry'/>
                    <ServicesBox setContent={this.setContent} href='/produkty/archivex'id='2' src='./img/iconArchiveX.png' title='ArchiveX' stage='Riešenie pre digitálne archívy' />
                    <ServicesBox setContent={this.setContent} href='/produkty/docminer' id='3' src='./img/iconDocMiner.png' title='DocMiner' stage='Riešenie pre vyťažovanie dát' />
                   <ServicesBox setContent={this.setContent} href='/produkty/mindfind' id='4' src='./img/iconMindFind.png' title='MindFind' stage='Riešenie pre spracovanie obrazu a detekciu objektov.' />
                </div>
                {!this.props.swipeState &&
                <ProductsContent swipeState={this.props.swipeState} currentContent = {this.state.currentContent} />
                    }

            </div>
        )
    }
}


export default ProductsScreen