import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  useParams
} from "react-router-dom";

const svgs = [
        require('./images/0.svg'),
        require('./images/1.svg'),
        require('./images/2.svg'),
        require('./images/3.svg'),
        require('./images/4.svg'),
        require('./images/5.svg')
]

const svgpaths = [
    './images/0.svg','./images/1.svg','./images/2.svg','./images/3.svg','./images/4.svg','./images/5.svg'
]


class App extends React.Component{
    constructor(){
        super()
        this.state = {
            activeSlide:1,
            pic : svgs[0]
        }
        this.setActiveSlide = this.setActiveSlide.bind(this)
        
    }
    
    componentDidMount(){
        svgs.forEach((picture) => {
        const img = new Image();
        img.src = picture;
    });
    }
    
    setActiveSlide(id){
        this.setState({activeSlide:id})
        setTimeout(()=>{this.setState({ pic:svgs[id-1]})},300)
    
    }
    
    render(){
        
        return(
            <Router>
            <div>
                <Menu 
                    activeSlide={this.state.activeSlide}
                    notifySlider={this.notifySlider}/>
                <div className='header'>
                    <Switch>
                        <Route path={"/"}>
                           
                            <SliderWrapper data={this.state}  setActiveSlide={this.setActiveSlide}>
                                
                            </SliderWrapper>
                        </Route>
                    </Switch>
                </div>
                <SlideUpButton />
                <img className='home-svg animated-svg' src={this.state.pic}></img>
            </div>
            </Router>
        )
    }
}

