function SliderWrapper(props){
    //setting document title
    setTitle()
    let {a} = useParams()
    let location = window.location.pathname.split('/')
    
    location.shift()
    let [ state, setState ] = useState({
        currentScreen : 1,
        previousScreen: 0,
        swipeState : 0,
        firstEntry : 1
    })
    
    
    
    let avoidClick = false
    let getCurrentScreen = () =>{
        let destinations = ['domov','sluzby','produkty','projekty','vyskum','kontakt']
        for (const [index, dest] of destinations.entries()) {
            if (dest==location[0]){
                return index + 1
            }
        }
        return 1
    }
    
    let notifySlider = (slideId) =>{
        if (state.currentScreen != slideId && !avoidClick){
            props.setActiveSlide(slideId)
            handleSwipe(slideId)
        }
    }
    
    let handleSwipe = (currentScreen)=>{
        if (!avoidClick){
            setState((prevState) => {
            if (currentScreen < state.currentScreen){
                return {
                       previousScreen: prevState.currentScreen,
                        swipeState:1,
                       currentScreen: currentScreen,
                        firstEntry: 0
                      }
            }
            else{
                return {
                       previousScreen: prevState.currentScreen,
                        swipeState:2,
                       currentScreen: currentScreen,
                        firstEntry: 0
                      }
            }
        })
        
        avoidClick = true
        
        setTimeout(() => {
        setState((prevState) => 
                 {
                return{
                    ...prevState,
                    swipeState:0
                }})
                avoidClick = false
                }, 500)
        }
    }
    notifySlider(getCurrentScreen())
    return (
        <div>
        <Slider data={{location:location,...state}}></Slider>
        </div>
    )
}


export default App
    
