import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import {withRouter} from 'react-router-dom'
import $ from 'jquery'

/**
 * Wraps the React Router Link component and creates a delay after the link is clicked.
 */
class DelayLink extends React.Component {
  static propTypes = {
    /**
     * Milliseconds to wait before registering the click.
     */
    delay:PropTypes.number,
      
    hideMenu: PropTypes.func,
    /**
     * Called after the link is clicked and before the delay timer starts.
     */
    onDelayStart: PropTypes.func,
    /**
     * Called after the delay timer ends.
     */
    onDelayEnd:   PropTypes.func
  };

  static defaultProps = {
    delay:        0,
    hideMenu:   () => {},
    onDelayStart: () => {},
    onDelayEnd:   () => {}
  };

  static contextTypes = Link.contextTypes;

  constructor(props) {
    super(props);
    this.timeout = null;
  }

    scrollToTop(){
        $("#root").stop().animate({
                scrollTop: 0
            }, this.props.delay, function() {
            /*setTimeout(()=>{$('.animated-svg').filter('.second').css('opacity',0)},100)*/
        })
    }

  componentWillUnmount() {
    if (this.timeout) {
      clearTimeout(this.timeout);
    }
  }

  /**
   * Called when the link is clicked
   *
   * @param {Event} e
   */
  handleClick = (e) => {
    window.didRoute = true;
    const { replace, to, delay, hideMenu, onDelayStart, onDelayEnd } = this.props;
    const history = this.props.history
    if (delay>0){
        this.scrollToTop()
    }
    this.props.hideMenu()
    if (e.defaultPrevented) {
      return;
    }
    e.preventDefault();
      setTimeout(()=>{
          $('.animated-svg').addClass('pre')
        setTimeout(()=>{$('.animated-svg').removeClass('pre')
                       },delay+300);
      },100);
      

    this.timeout = setTimeout(() => {
      if (replace) {
        history.replace(to);
      } else {
        history.push(to);
      }
        /*setTimeout(()=>{$('.animated-svg').filter('.second').css('opacity',1)},600)*/
    }, delay);

  };

  render() {
    const props = Object.assign({}, this.props);
    delete props.delay;
    delete props.onDelayStart;
    delete props.onDelayEnd;

    return (
      <Link {...props} onClick={this.handleClick} />
    );
  }
}
export default withRouter(DelayLink)