import React, {useState,useEffect,useRef} from 'react'

function SliderChooser(props){
    const [ slide, setSlide ] = useState(props.data.startId ? props.data.startId : 0)
    const [ swipeState, setSwipeState] = useState(0)
    const nOfElements = props.data.chooserItems.length
    const transitionLength = 300
    
    const sliderRef = useRef(null)
    
    useEffect(() => {
        const slider = sliderRef.current
        slider.addEventListener('touchstart', handleTouchStart, false)
        slider.addEventListener('touchmove', handleTouchMove, false)
        return () => {
            slider.removeEventListener("touchstart", handleTouchStart)
            slider.removeEventListener("touchstart", handleTouchMove)
        }
    })
    
    
    var xDown = null
    var yDown = null

    function getTouches(evt) {
        return evt.touches || 
            evt.originalEvent.touches 
    }

    function handleTouchStart(evt) {
        const firstTouch = getTouches(evt)[0];
        xDown = firstTouch.clientX
        yDown = firstTouch.clientY
    };

    async function swipeHandle(offset){
        setSwipeState(offset)
        await new Promise(r => setTimeout(r, transitionLength))
        setSwipeState(0)
        setSlide((prevSlide) => {
            let specialCase = null
            if (prevSlide + offset < 0 ){
                specialCase = nOfElements-1
            }
            else if (prevSlide + offset == nOfElements){
                specialCase = 0
            }
            return((specialCase!=null) ? specialCase : prevSlide + offset)
        })

    }

    function handleTouchMove(evt) {
        if (!xDown || !yDown) {
            return
        }

        var xUp = evt.touches[0].clientX
        var yUp = evt.touches[0].clientY

        var xDiff = xDown - xUp
        var yDiff = yDown - yUp

        if (Math.abs(xDiff) > Math.abs(yDiff)) {
            if (xDiff > 0) {
                swipeHandle(1)
            } else {
                swipeHandle(-1)
            }
        }
        xDown = null
        yDown = null
    }

    
    
    
    
    const sliderStyle = {
        marginLeft: `${-33*(1+swipeState)}%`,
        transition: swipeState ? 'all .3s': 'none'
    }
    
    let currentTexts = []
    for (let i=0;i<5;i++){
        currentTexts.push(props.data.chooserItems[(slide+i)%nOfElements])
    }
        
    const sliderItems = currentTexts.map((item,index) => {
            if (index==2){
                
            }
            return(<div className={(index==2 && !swipeState) ? 'active item' : 'item'}>{item.text}</div>)
        })
    props.data.onSwipe((slide+2)%nOfElements)
    return(
            <div className='sliderChooserCont'>
                <div className='arrowCont'>
                    <img onClick = {()=>{swipeHandle(-1)}} src={require('./img/arrowleft.png')}></img>
                </div>
                <div style={{overflow:'hidden',width:'100%',borderRadius:'20px'}}>
                    <div ref={sliderRef} style={sliderStyle} className='sliderChooser'>
                        {sliderItems}
                    </div>
                </div>
                <div className='arrowCont'>
                    <img onClick = {()=>{swipeHandle(1)}} src={require('./img/arrowright.png')}></img>
                </div>
            </div>
    )
}

export default SliderChooser